@import "normalize.css";
:root {
  --regular: 400;
  --medium: 500;
  --semi-bold: 600;
  --bold: 700;
  --extra-bold: 800;
  --big-fs: 24px;
  --medium-fs: 18px;
  --default-fs: 16px;
  --small-fs: 14px;
  --default-lh: 32px;
  --container-max-width: 1870px;
  --container-padding: 50px;
  --white-color: #FFF;
  --black-color: #000;
  --gray-color: #666;
  --orange-color: #FF9900;
  --light-orange-color: #FFB84D;
  --dark-orange-color: #E1670E;
  --light-gray-color: #CACACA;
  --light-green-color: #97EE3F;
  --transparent-color: transparent;
  --title-fs: 48px;
  --border-radius-very-small: 8px;
  --border-radius-small: 16px;
  --border-radius-big: 32px;
  --swiper-pagination-color: #fff;
  --swiper-pagination-bullet-width: 15px;
  --swiper-pagination-bullet-height: 15px;
  --swiper-pagination-bullet-horizontal-gap: 10px;
  --swiper-pagination-bullet-inactive-color: #cacaca;
  --swiper-pagination-bullet-inactive-opacity: .8;
}

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("MontserratRegular"), url("fonts/montserrat-v25-latin_cyrillic-regular.woff2") format("woff2"), url("fonts/montserrat-v25-latin_cyrillic-regular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("MontserratMedium"), url("fonts/montserrat-v25-latin_cyrillic-500.woff2") format("woff2"), url("fonts/montserrat-v25-latin_cyrillic-500.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("MontserratSemiBold"), url("fonts/montserrat-v25-latin_cyrillic-600.woff2") format("woff2"), url("fonts/montserrat-v25-latin_cyrillic-600.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("MontserratBold"), url("fonts/montserrat-v25-latin_cyrillic-700.woff2") format("woff2"), url("fonts/montserrat-v25-latin_cyrillic-700.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: local("MontserratExtraBold"), url("fonts/montserrat-v25-latin_cyrillic-800.woff2") format("woff2"), url("fonts/montserrat-v25-latin_cyrillic-800.woff") format("woff");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
svg {
  max-width: 100%;
}

body {
  min-width: 320px;
  font-family: "Montserrat", sans-serif;
  font-weight: var(--regular);
  font-size: var(--default-fs);
  line-height: var(--default-lh);
  background-color: var(--white-color);
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: var(--container-max-width);
  padding: 0 var(--container-padding);
  margin: 0 auto;
}

.btn {
  padding: 0;
  cursor: pointer;
}

.stop-scroll {
  overflow: hidden;
}

.section__title {
  margin: 0;
  font-size: var(--title-fs);
  line-height: 58.5px;
  font-weight: var(--regular);
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@media (any-hover: hover) {
  .nav__link:hover::after {
    transform: scaleX(1);
  }
  .search__btn:hover {
    color: var(--orange-color);
  }
  .search__submit-btn:hover {
    color: var(--orange-color);
  }
  .search__close-btn:hover {
    color: var(--orange-color);
  }
  .hero__btn:hover {
    background-color: var(--light-orange-color);
  }
  .card-first__btn:hover {
    border: var(--orange-color) solid 3px;
    background-color: var(--orange-color);
  }
  .work__btn:hover {
    color: var(--orange-color);
  }
  .work-card__btn-second:hover {
    border: solid 1px var(--gray-color);
    background-color: var(--gray-color);
    color: var(--white-color);
  }
  .work-card_btn-first:hover {
    background-color: var(--light-orange-color);
  }
  .faq__item:hover .faq__question {
    color: var(--orange-color);
  }
  .faq__btn:hover::before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    border-top: 1px solid var(--orange-color);
    border-bottom: 1px solid var(--orange-color);
    cursor: pointer;
    margin-top: -1px;
  }
  .faq__btn:hover .faq__svg {
    fill: var(--orange-color);
  }
  .faq__btn:hover .faq__svg path {
    fill: var(--white-color);
  }
  .footer-nav__link:hover {
    color: var(--orange-color);
  }
  .socials__link:hover {
    color: var(--orange-color);
  }
  .form__input:hover {
    background-color: var(--gray-color);
  }
  .form__btn:hover {
    background-color: var(--light-orange-color);
  }
}
.header {
  margin-bottom: 50px;
  padding-top: 14px;
  padding-bottom: 14px;
  background-color: var(--white-color);
  border-bottom-left-radius: var(--border-radius-big);
  border-bottom-right-radius: var(--border-radius-big);
  box-shadow: 0 5px 40px 0 rgba(0, 0, 0, 0.1);
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo:focus-visible {
  outline: 2px solid var(--orange-color);
  outline-offset: 3px;
}
.header__logo-img {
  display: flex;
  width: 225px;
  height: 71px;
}
.header__nav-search-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__nav {
  margin-right: 260px;
}
.header__search {
  position: relative;
}

.burger {
  display: none;
}

.nav__close-btn {
  display: none;
  background-color: var(--transparent-color);
  border: none;
  margin-bottom: 108px;
}
.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}
.nav__item:not(:last-child) {
  margin-right: 85px;
}
.nav__link {
  position: relative;
  font-size: var(--medium-fs);
  line-height: 22px;
  font-weight: var(--semi-bold);
  transition: color 0.2s linear;
}
.nav__link::after {
  content: "";
  position: absolute;
  bottom: -7px;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--orange-color);
  transition: transform 0.2s ease;
  transform: scaleX(0);
}
.nav__link:focus-visible {
  outline: solid var(--orange-color) 2px;
  outline-offset: 3px;
}
.nav__link:active {
  outline: none;
  color: var(--dark-orange-color);
}
.nav__link:active::after {
  background-color: var(--dark-orange-color);
  transform: scaleX(1);
}

.search__btn {
  width: 24px;
  height: 24px;
  background-color: var(--transparent-color);
  border: none;
  vertical-align: middle;
  transition: color 0.2s linear;
}
.search__btn:focus-visible {
  color: var(--black-color);
  outline: solid 2px var(--orange-color);
  outline-offset: 4px;
}
.search__btn:active {
  color: var(--orange-color);
  outline: none;
}
.search__btn.search__btn--active {
  opacity: 0;
}
.search__form {
  width: 539px;
  position: absolute;
  display: flex;
  align-items: center;
  padding: 19px 13px 16px 32px;
  border-radius: 24px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.12);
  z-index: 14;
  transform: translateY(-75%);
  background-color: var(--white-color);
  right: -8px;
  opacity: 0;
  visibility: hidden;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
.search__form.search__form--active {
  transform: translateY(-75%);
  opacity: 1;
  visibility: visible;
}
.search__input {
  margin-right: 14px;
  flex-grow: 1;
  padding-bottom: 7px;
  border: none;
  outline: none;
  border-bottom: solid var(--light-gray-color) 1px;
}
.search__input::placeholder {
  font-size: 18px;
  line-height: 22px;
  font-weight: var(--medium);
  color: var(--light-gray-color);
}
.search__input:focus-visible::placeholder {
  opacity: 0;
  transition: opacity 0.2s;
}
.search__close-btn {
  width: 24px;
  height: 24px;
  background-color: var(--transparent-color);
  border: none;
  color: var(--light-gray-color);
  transition: color 0.2s linear;
}
.search__close-btn:focus-visible {
  color: var(--light-gray-color);
  outline: solid 2px var(--orange-color);
  outline-offset: 4px;
}
.search__close-btn:active {
  color: var(--black-color);
  outline: none;
}
.search__submit-btn {
  width: 24px;
  height: 24px;
  background-color: var(--transparent-color);
  border: none;
  color: var(--black-color);
  margin-right: 20px;
  transition: color 0.2s linear;
}
.search__submit-btn:focus-visible {
  color: var(--black-color);
  outline: solid 2px var(--orange-color);
  outline-offset: 4px;
}
.search__submit-btn:active {
  color: var(--orange-color);
  outline: none;
}
.search__submit-svg {
  display: block;
}
.search__close-svg {
  display: block;
}

@media (max-width: 1440px) {
  .header__nav {
    margin-right: 100px;
  }
}
@media (max-width: 1200px) {
  .header {
    margin-bottom: 40px;
    padding-top: 26px;
    padding-bottom: 26px;
  }
  .header__logo-img {
    width: 150px;
  }
  .header__nav {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: 0;
    top: 0;
    min-width: 250px;
    height: 100vh;
    z-index: 1000;
    padding: 38px 248px 0 54px;
    background-color: var(--white-color);
    border-bottom-left-radius: 16px;
    border-bottom-right-radius: 16px;
    box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
    overflow-y: auto;
    visibility: hidden;
    transform: translateX(-120%);
    transition: visibility 0.3s ease-in-out, transform 0.3s ease-in-out;
  }
  .header__nav .nav__list {
    flex-direction: column;
  }
  .header__nav .nav__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 57px;
  }
  .header__nav .nav__link {
    font-size: var(--big-fs);
    line-height: 29.26px;
  }
  .header__nav.header__nav--active {
    visibility: visible;
    transform: none;
  }
  .nav__close-btn {
    display: block;
  }
  .nav__close-svg {
    display: block;
    width: 23px;
    height: 24px;
  }
  .burger {
    position: relative;
    display: block;
    width: 30px;
    height: 23px;
    border: none;
    background-color: var(--transparent-color);
  }
  .burger__line {
    position: absolute;
    left: 50%;
    width: 100%;
    height: 3px;
    background-color: var(--black-color);
    transform: translateX(-50%);
    transition: background-color 0.2s ease;
  }
  .burger__line:first-child {
    top: 0;
  }
  .burger__line:nth-child(2) {
    top: 50%;
    transform: translateY(-50%) translateX(-50%);
  }
  .burger__line:last-child {
    bottom: 0;
  }
  .burger:active {
    outline: none;
  }
  .burger:focus-visible {
    outline: 2px solid var(--orange-color);
    outline-offset: 3px;
  }
  .burger:active .burger__line {
    background-color: var(--dark-orange-color);
  }
}
@media (max-width: 800px) {
  .header {
    padding-top: 11px;
    padding-bottom: 10px;
    border-bottom-left-radius: var(--border-radius-medium);
    border-bottom-right-radius: var(--border-radius-medium);
  }
  .header__nav {
    padding: 22px 248px 0 54px;
  }
}
@media (max-width: 576px) {
  .header {
    margin-bottom: 30px;
    padding-top: 17px;
    padding-bottom: 17px;
    border-bottom-left-radius: var(--border-radius-small);
    border-bottom-right-radius: var(--border-radius-small);
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
  }
  .header__logo-img {
    width: 110px;
  }
  .header__nav {
    height: 71vh;
    padding: 22px 50% 0 25px;
  }
  .header__nav .nav__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .header__nav .nav__link {
    font-size: var(--default-fs);
    line-height: 19.5px;
  }
  .header__search {
    width: 100%;
    position: static;
  }
  .nav__close-svg {
    width: 23px;
    height: 23px;
  }
  .nav__close-btn {
    margin-bottom: 27px;
  }
  .search__form {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    padding: 22px 14px 17px 16px;
    width: 100%;
    transform: translateY(-75%);
    right: 0;
  }
  .search__input {
    width: 100%;
    margin-right: 1px;
    padding: 0 0 7px 0;
  }
  .search__input::placeholder {
    font-size: 16px;
    line-height: 19.5px;
  }
  .search__submit-btn {
    min-width: 24px;
    min-height: 24px;
  }
  .search__close-btn {
    min-width: 24px;
    min-height: 24px;
  }
}
@media (max-width: 320px) {
  .header__nav {
    width: 100%;
    height: 71vh;
    padding: 22px 97px 0 15px;
  }
}
.hero {
  margin-bottom: 50px;
}
.hero__wrapper {
  position: relative;
  padding: 100px 80px 101px 80px;
}
.hero__content {
  position: relative;
  max-width: 837px;
  z-index: 2;
}
.hero__title {
  margin: 0;
  margin-bottom: 24px;
  font-size: 70px;
  line-height: 85px;
  font-weight: var(--extra-bold);
  color: var(--white-color);
}
.hero__desc {
  margin: 0;
  margin-bottom: 90px;
  font-size: var(--big-fs);
  line-height: 29px;
  font-weight: var(--bold);
  color: var(--white-color);
}
.hero__btn {
  padding: 25px 39px;
  color: var(--white-color);
  font-size: 23px;
  line-height: 28px;
  font-weight: var(--bold);
  border-radius: var(--border-radius-small);
  border: none;
  background-color: var(--orange-color);
  transition: background-color 0.3s ease;
}
.hero__btn:focus-visible {
  outline: none;
  background-color: var(--light-orange-color);
}
.hero__btn:active {
  background-color: var(--dark-orange-color);
}
.hero__slider {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  border-radius: var(--border-radius-big);
  transform: translateZ(0);
}
.hero__slide {
  background-position: center;
  background-color: #8FC5E7;
  background-repeat: no-repeat;
  background-size: cover;
}
.hero__slide--one {
  background-image: url("img/hero-img-one.jpg");
}
.hero__slide--two {
  background-image: url("img/hero-img-two.jpg");
}
.hero__slide--three {
  background-image: url("img/hero-img-three.jpg");
}
.hero__pagination {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.swiper-pagination-bullet:focus-visible {
  outline: 2px solid var(--orange-color);
}

.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 20px;
}

@media (max-width: 1440px) {
  .hero {
    margin-bottom: 35px;
  }
  .hero__wrapper {
    padding: 160px 77px 160px 77px;
  }
  .hero__title {
    margin-bottom: 16px;
    font-size: 60px;
    line-height: 73.14px;
  }
  .hero__desc {
    margin-bottom: 60px;
    font-size: 20px;
    line-height: 24.38px;
  }
  .hero__slide--one {
    background-image: url("img/hero-img-one-1024.jpg");
  }
  .hero__slide--two {
    background-image: url("img/hero-img-two-1024.jpg");
  }
  .hero__slide--three {
    background-image: url("img/hero-img-three-1024.jpg");
  }
}
@media (max-width: 800px) {
  .hero__wrapper {
    padding: 160px 40px 160px 40px;
  }
  .hero__title {
    font-size: 50px;
    line-height: 60.95px;
  }
  .hero__desc {
    font-size: var(--medium-fs);
    line-height: 21.94px;
  }
  .hero__slider {
    border-radius: 24px;
  }
  .hero__slider--one {
    background-image: url("img/hero-img-one-768.jpg");
  }
  .hero__slider--two {
    background-image: url("img/hero-img-two-768.jpg");
  }
  .hero__slider--three {
    background-image: url("img/hero-img-three-768.jpg");
  }
}
@media (max-width: 576px) {
  :root {
    --swiper-pagination-bullet-width: 10px;
    --swiper-pagination-bullet-height: 10px;
  }
  .hero {
    margin-bottom: 61px;
  }
  .hero__wrapper {
    padding: 60px 21px 59px 22px;
  }
  .hero__title {
    margin-bottom: 8px;
    text-align: center;
    font-size: 20px;
    line-height: 24.38px;
  }
  .hero__desc {
    margin-bottom: 33px;
    text-align: center;
    font-size: 12px;
    line-height: 14.63px;
  }
  .hero__btn {
    display: block;
    margin: 0 auto;
    padding: 13px 28px;
    font-size: 12px;
    line-height: 14.63px;
    border-radius: var(--border-radius-very-small);
  }
  .hero__slider {
    border-radius: var(--border-radius-small);
  }
  .hero__slider--one {
    background-image: url("img/hero-img-one-320.jpg");
  }
  .hero__slider--two {
    background-image: url("img/hero-img-two-320.jpg");
  }
  .hero__slider--three {
    background-image: url("img/hero-img-three-320.jpg");
  }
  .swiper-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    bottom: 4px;
  }
}
@media (max-width: 320px) {
  .hero__wrapper {
    background-image: url("img/hero-img-one-320.jpg");
  }
}
.about {
  margin-bottom: 100px;
}
.about__title {
  margin-bottom: 21px;
}
.about__desc {
  max-width: 1235px;
  margin: 0;
  margin-bottom: 50px;
  font-weight: var(--regular);
}
.about__content {
  display: flex;
}
.about__block {
  display: flex;
  flex-direction: column;
  width: calc((100% - 32px) / 2);
}
.about__block:first-child {
  margin-right: 32px;
}
.about__block .about .about__item:first-child {
  margin-bottom: 32px;
}
.about__item {
  flex: 1;
}

.card {
  padding: 65px 115px 65px 299px;
  border-radius: var(--border-radius-big);
  border: var(--light-gray-color) solid 1px;
  background-repeat: no-repeat;
  background-position: 8.5% 50%;
}
.card__title {
  margin: 0;
  margin-bottom: 6px;
  font-size: var(--big-fs);
  line-height: 29.2px;
  font-weight: var(--bold);
}
.card__desc {
  max-width: 450px;
  margin: 0;
  font-weight: var(--regular);
}

.card-first {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-end;
  border-radius: var(--border-radius-big);
  background-image: url("img/about-img.jpg");
  background-repeat: no-repeat;
  background-color: var(--orange-color);
  background-size: cover;
  background-position: center;
}
.card-first__wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 50px;
}
.card-first__wrapper::before {
  content: "";
  display: block;
  position: absolute;
  background-color: var(--black-color);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.5;
  border-radius: var(--border-radius-big);
  z-index: 1;
}
.card-first__desc {
  max-width: 433px;
  margin: 0;
  margin-right: 30px;
  color: var(--white-color);
  font-weight: var(--bold);
  z-index: 2;
}
.card-first__btn {
  max-height: 60px;
  padding: 18px 35px;
  color: var(--white-color);
  border: var(--orange-color) solid 3px;
  border-radius: var(--border-radius-small);
  font-weight: var(--bold);
  line-height: 19.5px;
  background-color: var(--transparent-color);
  z-index: 2;
  transition: border 0.3s ease, background-color 0.2s ease;
}
.card-first__btn:focus-visible {
  color: var(--white-color);
  border: var(--orange-color) solid 3px;
  background-color: var(--orange-color);
  outline: none;
